@import "../dist/index.scss";

html,
body {
  font-size: 1.5rem;
  font-weight: 400;
  line-height: 2rem;
  text-transform: initial;
  letter-spacing: initial;
  font-weight: 400;
  color: #212b36;
  font-family: unquote($font-stack-base);
}
@media (min-width: 40em) {
  html,
  body {
    font-size: 1.4rem;
  }
}
html {
  position: relative;
  box-sizing: border-box;
  font-size: 62.5%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  text-size-adjust: 100%;
  text-rendering: optimizeLegibility;
}
body {
  min-height: 100%;
  margin: 0;
  padding: 0;
  background-color: #f4f6f8;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  font-size: 1em;
  font-weight: 400;
}
.banner,
.contentinfo {
  background: #f5f5f5;
}
.banner {
  padding: 1em 0;
}
.container {
  margin: 0 auto;
  padding: $spacing-base $spacing-extra-loose;
  max-width: 128rem;
}
.container--shadow {
  background: $color-white-base;
  border-radius: 6px;
  box-shadow: 0 1.2rem 3.6rem rgba(0, 0, 0, 0.2);
  margin-bottom: $spacing-extra-loose * 2;
}
h1 {
  margin: 0;
  font-size: 3rem;
  font-weight: normal;
  line-height: 2;
  padding-left: $spacing-loose;
}
section + section {
  margin-top: $spacing-extra-loose * 2;
}
table {
  table-layout: fixed;
  border-collapse: separate;
  border-spacing: 1rem;
  width: 100%;
}
th,
td {
  padding: 0 1rem;
  vertical-align: baseline;
  word-break: break-word;
  hyphens: auto;
}
th {
  font-weight: normal;
  text-align: left;
}
thead th {
  border-bottom: 1px solid $color-indigo-lighter;
  padding-top: 1rem;
  padding-bottom: 0.5rem;
  color: $color-ink-lighter;
}
thead th:first-child {
  font-size: 2rem;
  color: inherit;
}
code {
  font-family: unquote($font-stack-monospace);
}
.metric-box,
.radius-box {
  display: inline-block;
  vertical-align: middle;
  background: #eee;
}
.radius-box {
  width: 100%;
  height: 3rem;
}
.radius-box.borderRadiusCircle {
  width: 3rem;
}
.line-height-example {
  border-bottom: 1px solid #eee;
  background-image: -webkit-linear-gradient(#eee 1px, transparent 1px);
  background-image: linear-gradient(#eee 1px, transparent 1px);
  background-size: 100% 1rem;
}
